.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.history {
    display: flex;
    flex-direction: column-reverse;
    width: 70%;
    /* scroll; */
    overflow-y: auto;
    max-height: 100%;
}

.search-entry {
    align-self: flex-end;
}

.result-entry {
    align-self: flex-start;
}

#search-form {
    width: 75%;
    display: flex;
    margin-top: 25px;
    margin-bottom: 10px;
}

/* #search-input {
    flex: 1;
    border: none;
} */

.search-box {
    flex: 1;
    display: flex;
    width: 60%;
    border-radius: 2em;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    padding-left: 1.5em;
    padding-right: 0.5em;
    border: thin;
    border-style: solid;
    border-color: gray;
    border-right-width: 0;
}

.search-box:focus {
    box-shadow: 2px 2px 4px 1px rgba(218, 186, 2, 0.671);
    transition-property: box-shadow;
    transition-duration: 20ms;
    transition-timing-function: ease-in;
    border-color: rgb(218, 185, 2);
    outline: none;
}

.search-box:hover:not(:focus) {
    box-shadow: 2px 2px 4px 1px gray;
    transition-property: box-shadow;
    transition-duration: 20ms;
    transition-timing-function: ease-in;
}

.search-button {
    background-color: rgb(240, 204, 0);
    border-radius: 2em;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-style: solid;
    border: thin;
    border-color: rgb(218, 185, 2);
    padding-left: 1.25em;
    padding-right: 1.25em;
    text-align: center;
}

.search-button:active {
    box-shadow: 2px 2px 4px 1px rgba(218, 186, 2, 0.671);
    transition-property: box-shadow;
    transition-duration: 20ms;
    transition-timing-function: ease-in;
    border-color: rgb(218, 185, 2);
    outline: none;
}

.search-button:hover, 
.search-button:active {
    background-color: rgb(218, 185, 2);
}

.search-button:focus:not(:active), 
.search-button:hover:not(:active) {
    box-shadow: 2px 2px 4px 1px gray;
    transition-property: box-shadow;
    transition-duration: 20ms;
    transition-timing-function: ease-in;
}

.search-button:focus {
    outline: none;
}

