.history-item {
    background-color: lightyellow;
    border-radius: 5px;
    border-style: hidden;
    box-shadow: 3px 3px 3px 4px lightgray;
    /* width: 70%; */
    padding: 10px;
    margin-top: 7px;
    margin-right: 3px;
    margin-left: 3px;
    margin-bottom: 3px;
}

.question {
    padding: 5px;
    width: max-content;
    margin-right: auto;
    background-color: gold;
    border-top-left-radius: 0px;
    border-radius: 5px;
    border-style: hidden;
    box-shadow: 3px 3px 3px lightgray;
}

.answer {
    padding: 5px;
    width: max-content;
    margin-left: auto;
    margin-bottom: 0;
    background-color: white;
    border-top-right-radius: 0px;
    border-radius: 5px;
    border-style: hidden;
    box-shadow: 3px 3px 3px lightgray;
}